import * as React from "react";
import styled from "styled-components";
import * as PropTypes from "prop-types";
import Blockie from "./Blockie";
import Banner from "./Banner";

import ImgProject from "../assets/img/project/img.png"
import Marquee from "react-fast-marquee";
import FadeIn from 'react-fade-in';

import metaboys from '../assets/img/marquee .png'
import '../assets/css/project.css'
import LogoMarquee from "./LogoMarquee";

interface IRegisterModalProps {
  killSession: () => void;
  balance: number;
  address: string;
  hideModal: () => void;
  show: boolean;
}

const RegisterModal = (props: IRegisterModalProps) => {
  const { show, hideModal, address, balance, killSession } = props;
  console.log("address: ", address, " ", balance)
  const [form, setForm] = React.useState({
    email: "",
    wallet: address,
    balance: balance,
  });
  const onSubmit = async (e:any) => {
    e.preventDefault();

    // When a post request is sent to the create url, we'll add a new record to the database.
    const newPerson = { ...form };

    await fetch("/user/add", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(newPerson),
    })
    .then( async (res:any) => {
      if(res.status === 400){
        let resp = await res.json()
        let error = parseInt( resp["message"] )
        console.log(error)
        if (error === 0){
          return window.alert("Email Existed")
        }
        if (error === 1){
          return window.alert("Wallet Existed")
        }
      }
    })
    .catch(error => {
      window.alert(error);
      return;
    });

    setForm({ email: "", wallet: "", balance: 0 });
    hideModal()
  }

  // These methods will update the state properties.
  const updateForm = (value:Object) => {
    return setForm((prev) => {
      return { ...prev, ...value };
    });
  }

  return (
    <div className="modal" id="registerModal" style={{display: show===true?"block":"none"}}>
        <div className="modal-dialog">
        <div className="modal-content">
    
            <div className="modal-header">
              <h4 className="modal-title">Modal Heading</h4>
              <button type="button" className="btn-close" onClick={killSession}></button>
            </div>
    
            <div className="modal-body">
            <form onSubmit={onSubmit}>
              <div className="mb-3 mt-3">
                <input type="email" className="form-control"
                  placeholder="Enter username" 
                  value={form.email}
                  onChange={(e) => updateForm({ email: e.target.value, wallet:address, balance: balance })}
                  name="uname" required />
              </div>
              <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            </div>
    
            <div className="modal-footer">
              <button type="button" className="btn btn-danger" onClick={killSession}>Close</button>
            </div>
    
        </div>
        </div>
    </div>
  );
};

export default RegisterModal;
